import React from "react";
import styled from 'styled-components';

import {List, Button, Popconfirm} from 'antd';
import {DeleteOutlined, PushpinTwoTone} from '@ant-design/icons';

const StyledList = styled(List)`
    padding: 16px;
`;

const DeadlineList = ({data, onPin, onDelete}) => {
    const formatDate = date => `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
    const formatDescription = item => `${formatDate(new Date(item.start))} ~ ${formatDate(new Date(item.end))}`;

    return (
        <StyledList
            itemLayout="horizontal"
            dataSource={data}
            renderItem={(item, index) => (
                <List.Item
                    actions={[
                        <Popconfirm
                            title="Are you sure？"
                            okText="Yes"
                            placement="left"
                            cancelText="No"
                            onConfirm={() => onPin(item.id, index)}>
                            <PushpinTwoTone />
                        </Popconfirm>,
                        <Popconfirm
                            title="Are you sure？"
                            okText="Yes"
                            placement="left"
                            cancelText="No"
                            onConfirm={() => onDelete(item.id, index)}>
                            <Button
                                type="primary"
                                danger
                                shape="circle"
                                icon={
                                    <DeleteOutlined />
                                }/>
                        </Popconfirm>]}>
                    <List.Item.Meta
                        title={item.name}
                        description={formatDescription(item)}
                    />
                </List.Item>
            )}
        />
    );
}

export default DeadlineList;